<script setup>
	import {
		useUserStore
	} from "../../stores/user.js"
	import {onLoad} from "@dcloudio/uni-app"
	import {ref} from "vue"
	const useUser = useUserStore()
	const queryParam=ref({})
	const categoryList=ref([{id:1,paramName:"全部",paramValue:""}])
	const back2log = () => {

		uni.redirectTo({
			url: '/pages/login'
		})
	}
	const safeTop=ref(0)
	onLoad(async()=>{
		let {safeArea}=uni.getSystemInfoSync()
		safeTop.value=safeArea.top+10
		let res=await baseApi.getParamListByBaseName("sex")
		categoryList.value = res.data.data
	
	})
</script>

<template>
	<view style="text-align: center;">
		<view class="top" :style="{paddingTop: safeTop+'px'}">
			
		Welcome, {{useUser.user.nickname}}
		<uni-search-bar v-model="queryParam.title" placeholder="Explore starts from here..." radius="100" clearButton="auto" cancelButton="none" @confirm=""></uni-search-bar>
		
		</view>
		
		<button @click="back2log" >Login</button>
		
	</view>
</template>


<style>
	.top{
		background-color: rosybrown;
	}
</style>